.EmptySearchResults-container {
    background-color: #FAFAFA;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 24px;
    border-radius: 2rem;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
  }

.EmptySearchResults-container p {
    color: gray;
    text-align: center;
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;}